/* #Navigation
================================================== */

.start-header {
	font-family: "Raleway", sans-serif;
	font-weight: 800;
	opacity: 1;
	transform: translateY(0);
	padding: 16px 0;
	box-shadow: 0 10px 30px 0 rgba(138, 155, 165, 0.15);
	transition : all 0.3s ease-out;
}

@media (max-width:991px){
	.start-header {
		opacity: 1;
		transform: translateY(0);
		padding: 1px 0;
		box-shadow: 0 10px 30px 0 rgba(138, 155, 165, 0.15);
		transition : all 0.3s ease-out;
	}
}
.start-header.scroll-on {
	box-shadow: 0 5px 10px 0 rgba(138, 155, 165, 0.15);
	padding: 10px 0;
	transition : all 0.3s ease-out;
}
.start-header.scroll-on .navbar-brand img{
	height: 24px;
	transition : all 0.3s ease-out;
}
.navigation-wrap{
	position: relative;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 1000;
	transition : all 0.3s ease-out;
}
.navbar{
	padding: 0;
}
.navbar-brand img{
	
	height: 28px;
	width: auto;
	display: block;
  -webkit-filter: brightness(10%);
          filter: brightness(10%);
	transition : all 0.3s ease-out;
}
.navbar_navbar-toggler {
	float: right;
	border: none;
	padding-right: 0;
}
.navbar_navbar-toggler:active,
.navbar_navbar-toggler:focus {
	outline: none;
}
.navbar-light .navbar_navbar-toggler-icon {
	width: 24px;
	height: 17px;
	background-image: none;
	position: relative;
	border-bottom: 1px solid #000;
    transition: all 300ms linear;
}
.navbar-light .navbar_navbar-toggler-icon:after,
.navbar-light .navbar_navbar-toggler-icon:before{
	width: 24px;
	position: absolute;
	height: 1px;
	background-color: #000;
	top: 0;
	left: 0;
	content: '';
	z-index: 2;
    transition: all 300ms linear;
}
.navbar-light .navbar_navbar-toggler-icon:after{
	top: 8px;
}
.navbar_navbar-toggler[aria-expanded="true"] .navbar_navbar-toggler-icon:after {
	transform: rotate(45deg);
}
.navbar_navbar-toggler[aria-expanded="true"] .navbar_navbar-toggler-icon:before {
	transform: translateY(8px) rotate(-45deg);
}
.navbar_navbar-toggler[aria-expanded="true"] .navbar_navbar-toggler-icon {
	border-color: transparent;
}
.navbar_nav-link{
	color: #212121 !important;
	font-weight: 500;
    transition: all 200ms linear;
}
.navbar_nav-item:hover .navbar_nav-link{
	color: #8167a9 !important;
}
.navbar_nav-item.active .navbar_nav-link{
	color: #777 !important;
}
.navbar_nav-link {
	position: relative;
	padding: 5px 0 !important;
	display: inline-block;
}
.navbar_nav-item:after{
	position: absolute;
	bottom: -5px;
	left: 0;
	width: 100%;
	height: 2px;
	content: '';
	background-color: #8167a9;
	opacity: 0;
    transition: all 200ms linear;
}
.navbar_nav-item:hover:after{
	bottom: 0;
	opacity: 1;
}
.navbar_nav-item.active:hover:after{
	opacity: 0;
}
.navbar_nav-item{
	position: relative;
    transition: all 200ms linear;
}

