  .twitter-typeahead {
    display: block!important;
    width: 100%;
    z-index: 2;
  }
  
  .tt-menu {
    width: 100%;
    margin-top: -1px;
    background-color: #FFFFFF;
    border: 1px solid #D9D9D9;
    border-radius: 0 0 3px 3px;
  }
  
  .tt-menu:before {
    content: '';
    position: absolute;
    top: -4px;
    left: -1px;
    width: calc(100% + 2px);
