.el\:m-attribute {
    box-sizing: border-box;
    display: flex;
    align-items: center
}

.el\:m-attribute--inline {
    display: inline-flex
}

.el\:m-attribute__icon {
    position: relative;
    font-size: 1em;
    width: .75em;
    height: .75em;
    transform: none;
    transition: all .5s ease 0s;
    flex: 0 0 0.75em;
    margin-right: .5em
}

.el\:m-attribute__icon--bordered {
    border: 1px solid #d9d9e0;
    border-radius: 50%
}

.el\:m-attribute__label {
    display: inline-block;
    max-height: 1em;
    line-height: 1em;
    overflow: hidden;
    max-width: calc(100% - 1.25em);
    white-space: nowrap;
    text-overflow: ellipsis
}

.el\:m-attribute__label>* {
    font-size: .625em
}

.el\:m-attribute--shrink {
    min-width: 0
}

.el\:m-attribute--stretch .el\:m-attribute__label {
    max-width: none
}

.el\:m-avatar {
    position: relative;
    display: inline-block;
    vertical-align: middle
}

.el\:m-avatar input[type=file] {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 9
}

.el\:m-avatar__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin: auto
}

.el\:m-avatar--circle .el\:m-avatar__overlay {
    border-radius: 50%
}

.el\:m-avatar__overlay:hover {
    background-color: #027aff;
    opacity: .8
}

.el\:m-avatar__overlay--upload-icon:hover:before {
    content: "\F107";
    color: #fff;
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    font-size: 1.5em;
    line-height: 1em;
    width: 1em;
    height: 1em
}

.el\:m-avatar--circle,.el\:m-avatar--circle>img {
    border-radius: 50%
}

.el\:m-avatar--1x1em,.el\:m-avatar--1x1em>img {
    width: 1em;
    height: 1em
}

.el\:m-avatar--2x2em,.el\:m-avatar--2x2em>img {
    width: 2em;
    height: 2em
}

.el\:m-avatar--3x3em,.el\:m-avatar--3x3em>img {
    width: 3em;
    height: 3em
}

.el\:m-avatar--4x4em,.el\:m-avatar--4x4em>img {
    width: 4em;
    height: 4em
}

.el\:m-avatar--5x5em,.el\:m-avatar--5x5em>img {
