  height: 2rem;
}

.crayons-avatar--xl {
  width: 3rem;
  height: 3rem;
}

.crayons-avatar--2xl {
  width: 4rem;
  height: 4rem;
}

.crayons-avatar--3xl {
  width: 8rem;
