}

.crayons-logo {
  display: inline-block;
  border-radius: 3px;
  position: relative;
  background-color: --body-color-inverted;
  width: 1.5rem;
  height: 1.5rem;
}

.crayons-logo__image {
  border-radius: 3px;
  width: 100%;
  height: 100%;
  display: inline-block;
  vertical-align: bottom;
}

.crayons-logo::after {
  content: '';
  border: 1px solid --body-color;
  opacity: 0.15;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 3px;
  pointer-events: none;
}



.crayons-card {
  border-radius: 3px;
  background: #fff !important;
  color: #08090a;
  box-shadow: 0 0 0 1px rgba(8, 9, 10, 0.1);
}




:root {
  --article-rythm: 0.5rem;
  --article-padding-x: 0.75rem;
  --article-padding-y: 0.75rem;
  --article-font-size: 1.125rem;
}

@media (min-width: 640px) {
  :root {
    --article-padding-x: 1.25rem;
    --article-padding-y: 1.25rem;
  }
}

@media (min-width: 768px) {
  :root {
    --article-rythm: 1rem;
    --article-padding-x: 3rem;
    --article-padding-y: 2rem;
    --article-font-size: 1.25rem;
  }
}

@media (min-width: 1280px) {
  :root {
    --article-padding-x: 4rem;
  }
}

.article-wrapper {
  min-width: 0;
}

.crayons-article__cover {
  position: relative;
